        display: none;
    }
    .image1, .image2, .image3, .image4, .image5, .image6 {
        width: 60px; 
    height: 60px;
    margin: 5px;
    }
    .image1:hover, .image2:hover, .image3:hover, .image4:hover, .image5:hover, .image6:hover {
        -webkit-transform: scale(4);
                transform: scale(4);
    }

      .one {
